.Post {
    width: 100%;
    padding: .5rem;
    height: 34rem;
    overflow: hidden;
    position: relative;
    margin-bottom: 3rem;
    box-shadow: 0px -7px 7px 0px rgba(var(--baseClr), .2);
}

.Post:after {
    content  : "";
    position : absolute;
    z-index  : 1;
    bottom   : 0;
    left     : 0;
    pointer-events   : none;
    background-image : linear-gradient(to bottom, 
                      rgba(255,255,255, 0), 
                      rgba(var(--backClr), 1) 50%);
    width    : 100%;
    height   : 4em;
}

.FullPost {
    width: 100%;
    padding: 1rem;
}

.PostTitle {
    margin: 1rem 0;
    padding-right: 1rem;
    position: relative;
}

.EditButton {
    position: absolute;
    height: 1rem;
    width: 1rem;
    background: url('../../../assets/images/edit.svg') center center / contain no-repeat;
    top: calc(50% - .5rem);
    right: 0;
    cursor: pointer;
}

.Img {
    max-height: 22rem;
    width: auto;
    max-width: 100%;
}

.PostText {
    margin: 1rem 0;
    white-space: pre-wrap;
}

.OpenFullpost {
    position: absolute;
    bottom: 0;
    right: .5rem;
    z-index: 2;
    font-weight: 600;
    color: #3C1874;
    cursor: pointer;
}

.OpenFullpost:hover {
    color: #DE354C;
}

@media (max-width: 600px) {
    .OpenFullpost {
        font-size: 1.4rem;
    }
}