.Modal {
    position: fixed;
    z-index: 999;
    background-color: rgb(var(--backClr));
    width: 80%;
    height: 85%;
    border: 1px solid #ccc;
    left: 10%;
    top: 5%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    text-align: center;
}

.ModalContent {
    padding: 2rem;
    overflow: auto;
    height: 100%;
}

.CloseButton {
    position: fixed;
    left: .5rem;
    top: .5rem;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 50%;
    background-color: rgb(var(--backClr));
    background: url('../../../assets/images/close-btn.svg') center center / contain no-repeat;
    cursor: pointer;
}

.CloseButton:hover {
    background-color: rgba(var(--baseClr), .1);
}

@media (max-width: 600px) {
    .Modal {
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }

    .CloseButton {
        width: 2rem;
        height: 2rem;
    }
}

@media (min-width: 1025px) {
    .ModalContent p {
        max-width: 800px;
    }
}