.NavItem {
    display: flex;
    align-items: center;
    margin-left: .3rem;
    border-radius: .15rem;
}
.NavItem a {
    text-decoration: none;
    display: flex;
    align-items: center;
    margin: .75rem .5rem;
    color: rgb(var(--backClr));
    border: 1px solid transparent;
}

/* .NavItem a.active {
    color: rgb(171 188 206);
} */

@media (min-width: 1366px) {
    .NavItem a:active,
    .NavItem a:hover {
        border-bottom: 1px solid rgb(var(--backClr));
    }
}