.NavigationItems {
    list-style: none;
    display: flex;
    justify-content: flex-end;
    background-color: white;
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 997;
    padding: .5rem .75rem;
    background-color: rgb(var(--baseClr));
}

.NavLogo {
    background: url('../../../assets/images/logo.svg') center center / contain no-repeat;
    height: 2.5rem;
    width: 2.5rem;
}

.NavigationItems > li:first-child a:hover {
    border-bottom: 1px solid transparent;
}

.NavigationItems > li:first-child a {
    margin: 0;
}

.NavigationItems > li:last-child {
    border: 1px solid rgb(var(--backClr));
    margin-left: .6rem;
}

.NavigationItems > li:last-child a {
    margin: 0;
    padding: .75rem .5rem;
}

@media (min-width: 1366px) {
    .NavigationItems > li:last-child:hover {
        background-color: rgb(var(--backClr));
        cursor: pointer;
    }
    
    .NavigationItems > li:last-child:hover a {
        color: rgb(var(--baseClr));
    }
}